.alternatives {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.alternative {
  background:
#ffb300;
padding: 10px 20px;
margin: 11px;
border: 3px solid #fff;
border-radius: 10px;
background-size: 120% 120%;
animation: gradientBG 2s ease infinite;
box-shadow:0px 10px 10px rgba(0, 0, 0, 0.17);
font-weight: bold; 
}


