.alternative:nth-child(1) {
  background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgba(255,122,78,1) 0%, rgba(255,205,112,1) 90% );
}

.alternative:nth-child(2) {
  background-image: radial-gradient( circle farthest-corner at 90% 20%,  rgba(255,122,78,1) 0%, rgba(255,205,112,1) 90% );
}

.alternative:nth-child(3) {
  background-image: radial-gradient( circle farthest-corner at 10% 80%,  rgba(255,122,78,1) 0%, rgba(255,205,112,1) 90% );
}

.alternative:nth-child(4) {
  background-image: radial-gradient( circle farthest-corner at 90% 80%,  rgba(255,122,78,1) 0%, rgba(255,205,112,1) 90% );
}

.alternative.isMine {
  background-image: linear-gradient( 108.3deg,  rgba(202,73,118,1) 15.2%, rgba(255,84,84,1) 99.3% );
}

.alternative.isCorrect {
  background-image: linear-gradient( 107deg,  rgba(13,198,180,1) 8.1%, rgba(33,198,138,1) 79.5% );
}

.alternative.isCorrect.isMine {
  background-image: linear-gradient( 107deg,  rgba(13,198,180,1) 8.1%, rgba(33,198,138,1) 79.5% );
  color: #333;
  border: 3px solid white;
}

button {
  background: linear-gradient(to right, #56ccf2, #2f80ed);
    padding: 10px 60px;
    border: 2px solid white;
    border-radius: 7px;
    color: white;
    font-size: 1.3rem;
}



.timerbar {
  width: 100%;
  background: white;
  border: 1px solid white;
  position: relative;
  height:8px;
  margin-top:20px;
}

.timerbar:before {
  content:'';
  position: absolute;
  top: 0; left: 0; bottom: 0; right: 0;
  background: red;
  animation: toZero 10s linear;
  animation-fill-mode: forwards;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.timerbar .value {
  z-index: 1;
  background: white;
  display: inline-block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  color: black;
  text-align: center;
  vertical-align: middle;
  line-height: 40px;
}

.timerbar .value span {
  display: inline-block;
  animation: pulse-1 0.5s ease-in;
}

.timerbar .value span.anim0 {
  animation: pulse-0 0.5s ease-in;

}


@keyframes pulse-0 {
  0% {
    transform: scale(1.4);
  }
  100% {
    transform: scale(1.0);
  }
}
@keyframes pulse-1 {
  0% {
    transform: scale(1.4);
  }
  100% {
    transform: scale(1.0);
  }
}

@keyframes toZero {
  0% {
    width:100%;
    background: green;
    box-shadow: 0px 0px 10px green;
  }
  50% {
    background:yellow;
    /* transform: scaleY(1); */
    box-shadow: 0px 0px 10px yellow;
  }
  100% {
    width: 0%;
    background:red;
    /* transform: scaleY(2); */
    box-shadow: 0px 0px 10px 5px red;
  }
}


